import React from 'react';
import './Gallery.css'

const Gallery=()=>{
const source=[{src:'https://images.pexels.com/photos/1640777/pexels-photo-1640777.jpeg?cs=srgb&dl=pexels-ella-olsson-1640777.jpg&fm=jpg'},
              {src:'https://images.pexels.com/photos/1327503/pexels-photo-1327503.jpeg?cs=srgb&dl=pexels-hammad-khalid-1327503.jpg&fm=jpg'},
             {src:'https://images.pexels.com/photos/2229490/pexels-photo-2229490.jpeg?cs=srgb&dl=pexels-marcelo-chagas-2229490.jpg&fm=jpg'},
             {src:'https://images.pexels.com/photos/708488/pexels-photo-708488.jpeg?cs=srgb&dl=pexels-sydney-troxell-708488.jpg&fm=jpg'}]
return(
    <section className='section' id='gallery'>
        <h1 id='title'>Gallery</h1>
        <section className="gallery--section">
         {source.map((obj,ind)=>{
             return(
                <div className="gallery" key={obj.ind}>
                <div className="image">
                    <img src={obj.src} />
                </div>
               </div>
             );
         })}
        </section>
    </section>
)
}


export default Gallery;