import React from 'react';
import './Contact.css';

const Contact=()=>{

    const fields=[{label:'Name',id:'name',placeholder:'Fullname',icon:'fa fa-user'},{label:'Email',id:'email',placeholder:'someone@gmail.com',icon:'fa fa-envelope'}];
    return(
        <section className='section' id='contact'>
            <h1 id='title'>Contact</h1>
            <div className='contact--section'>
              <div className='contact--part'>
                   <label id='animate'>
                       <span>Do</span>
                       <span>n't</span><br/>
                       <span>Qu</span>
                       <span>it</span>
                   </label>
              </div>
              <div className='contact--part'>
              {
                  fields.map((obj,ind)=>{
                   return( <div className='field' id={ind}>
                   <h3>{obj.label}</h3>  
                     <div className='input'>
                        <i className={obj.icon}></i>
                        <input type='text' placeholder={obj.placeholder}/>
                    </div>
                   </div>)
                  })
              }
             <div className='field'>
                   <h3>Mesage</h3>
                   <div className='input'>
                        <i className='fa fa-pencil'></i>
                        <input type='text' placeholder='Message'/>
                    </div>  
             </div>
              <div className='button'>
                <button id='clear'>Clear</button>
                <button id='send'>Send</button>
              </div>
             </div>
            </div>
            <ul className="social">
            	<a href="#"><i class="fa fa-facebook"></i></a>
            	<a href="#"><i class="fa fa-twitter"></i></a>
            	<a href="#"><i class="fa fa-instagram"></i></a>
            	<a href="#"><i class="fa fa-youtube-play"></i></a>
            </ul>
        </section>
    )

}

export default Contact;