import React from 'react';
import { Home } from './Modules';
import './About.css';

const About=()=>{
const boxA=[{title:'Name',icon:'fa fa-user',text:'Nikhil Gupta'},
            {title:'Phone',icon:'fa fa-phone',text:'6205029701'},
            {title:'Address',icon:'fa fa-address-card',text:'Siwan Bihar'}
        ];
const boxB=[{title:'Email',icon:'fa fa-envelope',text:'nikhilmadheshya@gmail.com'},
        {title:'Date Of Birth',icon:'fa fa-calendar',text:'11 November 1998'},
        {title:'Nationality',icon:'fa fa-flag',text:'India'}
    ];
    
 return(
     <section className='section' id='about'>
         <h1 >About Me</h1>
     <div className='content--box'>
         <div className='img-box'>
         <div className='profile'>
             <img src='./images/nehal.jpg'/>
          </div>   
             <label className='line--border'></label>
             <label className='cover--border'></label>
         </div>
         <div className='box'>
          {boxA.map((obj,ind)=>{ 
              return(
              <div className='data' key={ind}>
                  <div className='icon'>
                      <i className={obj.icon}></i>
                  </div>
                  <div className='text'>
                    <div className='content'>
                       <h3>{obj.title}</h3>
                       <p>{obj.text}</p>
                    </div>
                  </div>
              </div>
              )
           })}
         </div>
         <div className='box'>
          {boxB.map((obj,ind)=>{ 
              return(
              <div className='data' key={ind}>
                  <div className='icon'>
                      <i className={obj.icon}></i>
                  </div>
                  <div className='text'>
                    <div className='content'>
                       <h3>{obj.title}</h3>
                       <p>{obj.text}</p>
                    </div>
                  </div>
              </div>
              )
           })}
         </div>
     </div>
     <div className='summary'>
             <p>My name is Nikhil Gupta. 
             I am passionate,friendly and kind type of person and do not like to be involved in politics. 
             I am learning designing and developing website from 2 years.</p>
             <button id='modal' style={{'fontFamily':'cursive'}}>NikhilGupta</button>    
    </div>

     </section>
 )   

};


export default About;