import React from 'react';
import './Skills.css';
import Objective from './Objective';

export default class Skills extends React.Component
{
    constructor(){ super();
    this.state={
        frontend:[
            {techno:'HTML',percentage:90,startup:0,background:'purple'},  {techno:'CSS',percentage:70,startup:0,background:'pink'},
            {techno:'Javascript',percentage:80,startup:0,background:'yellow'},  {techno:'React',percentage:50,startup:0,background:'cyan'},
            {techno:'SASS',percentage:60,startup:0,background:'#ff0066'}
        ],
        backend:[
            {techno:'NodeJS',percentage:60,startup:0,background:'#33ff33'},
            {techno:'JSON',percentage:80,startup:0,background:'orangered'},
            {techno:'Ajax',percentage:80,startup:0,background:'blue'}, 
        ]
    }

}

render()
{
    return(
        <>
        <section className='section' id='skills'>
            <h1 id='title'>Skills</h1>
            <section class='skills--container'>
            <div className='skills--part'>
                 <h2>Frontend</h2>
               <div className='content'>
                   {this.state.frontend.map((obj,ind)=>{
                    return(
                        <div className='technology' key={ind}>
                         <label>{obj.techno}</label>
                         <div className='process-bar'>
                             <span className='water' style={{'width':`${obj.percentage}%`,'background':`${obj.background}`}}></span>
                         </div>
                          <p>{obj.percentage}%</p>
                        </div>
                    ) 
                   })}
               </div>
             </div>
             <div className='skills--part'>
             <h2>Backend</h2>
             <div className='content'>
                   {this.state.backend.map((obj,ind)=>{
                    return(
                        <div className='technology' key={ind}>
                         <label>{obj.techno}</label>
                         <div className='process-bar'>
                             <span className='water' style={{'width':`${obj.percentage}%`,'background':`${obj.background}`}}></span>
                         </div>
                          <p>{obj.percentage}%</p>
                        </div>
                    ) 
                   })}
               </div>
                     </div>
                     
            </section>
        </section>
        <section class='section' id='strategy'>
              <h1 >Strategy</h1>
               <Objective></Objective>  
        </section>
        </>
    )
}

}