import React from 'react';
import { useState } from 'react';
import './Objective.css';

const Objectives=()=>{

    const [contents,setContent]=useState([
        {
            logo:'fa fa-pencil',
            title:'Objective',
            text:'Seeking a carrer that is more innovative and interesting job that give me oppurtinities to learn and enhance my skills and strength.'
        }
        ,
        {
            logo:'fa fa-tasks',
            title:'Weakness',
            text:'     To start with my weakness, I am a perfectionist so I tend to perhaps spend a little too much in completing my projects & checking it. '
        },
        {
            logo:'fa fa-star',
            title:'Strength',
            text:' Well,talking about my strength I am quick learner and curious to learn and adopt new technologies although my marks is not so good. '
        }
    ]);
  
    return(
     <div className='objective--strength'>
       {
           contents.map((obj,ind)=>(
           <div className='box'>
            <i className={obj.logo}></i>
           <h3>{obj.title}</h3>
           <p>{obj.text}</p>
           </div>))
       }
     </div>
    );
}

export default Objectives;